import React, { Component } from 'react';
import ReactTable from 'react-table';
import logo from './logo.svg';
import './App.css';
import 'react-table/react-table.css'

class App extends Component {
  constructor () {
    super();
    this.state = {
      lots: []
    };

    // this.handleLotIDChange  = this.handleLotIDChange.bind(this);
    // this.handleMakeChange   = this.handleMakeChange.bind(this);
    // this.handleModelChange  = this.handleModelChange.bind(this);

    this.handleSubmit       = this.handleSubmit.bind(this);

  }

  

  render(){
   
    // let tableHeader;
    // if (this.state.lots) {
    //   tableHeader = <table >
    // }


    return(
      <div id="lot-container">
        <form id="search" onSubmit={this.handleSubmit}>
          <label>Enter LotID:</label>
          <input type="text" name="lotidr" placeholder="lotID" value={this.state.lotidr} />
          <label>Enter make:</label>
          <input type="text" name="maker" placeholder="producent" value={this.state.maker} />
          <label>Enter model:</label>
          <input type="text" name="modelr" placeholder="model" value={this.state.modelr} />
          <input type="submit" value="Find lots" />
        </form>
        <div>
        </div>
        <div className="container1">
          {this.state.lots}
        </div>
      </div>
     
    );
  }


  handleSubmit = function (event) {
    event.preventDefault();
    var queryString = '';

    this.setState({lotID: this.state.lotidr})



    fetch('http://ec2-34-255-193-204.eu-west-1.compute.amazonaws.com:3001/api/lots').then((res) => {
      return res.json();
    }).then((json) => {
      console.log(json);
      const lots = <ReactTable
        
        data={json}
        
        columns={[
          {
            Header: "LotID",
            accessor: "lotID"
          },
          {
            Header: "Producent",
            accessor: "make"
          },
          {
            Header: "Model",
            accessor: "model"
          },
          {
            Header: "Rok",
            accessor: "year"
          },
          {
            Header: "Cena",
            accessor: "price"
          },
          {
            Header: "PL",
            accessor: "plPrice"
          },
          {
            Header: "UA",
            accessor: "uaPrice"
          },
          {
            Header: "Primary",
            accessor: "details.Primary Damage"
          },
          {
            Header: "Secondary",
            accessor: "details.Secondary Damage"
          },
          {
            Header: "Lokacja",
            accessor: "location"
          }
        ]}
        defaultPageSize={20}
        className="-striped -highlight"

      />
      this.setState({ lots: lots });
      console.log(this.state.lots);
    });
  }
}

export default App;



